.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.load-more {
  cursor: pointer;
  width: 150px;
  height: 50px;
  color: #fafafa;
  background-color: #4e60b6;
  border: none;
  border-radius: 10px;
  margin: 20px auto;
  display: block;
}

.hide {
  display: none;
}

.photo-card {
  border-radius: 10px;
  flex-direction: column;
  flex-basis: calc(25% - 12.5px);
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 16px -7px rgba(0, 0, 0, .75);
}

.photo-card a {
  height: 300px;
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.up {
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius: 50%;
  display: block;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.search-form {
  width: 100%;
  background-color: #4e60b6;
  justify-content: center;
  margin-bottom: 10px;
  padding: 30px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

input {
  height: 40px;
  width: 20%;
  border: 2px solid #4e60b6;
  border-right: none;
  border-radius: 10px 0 0 10px;
  outline: 0;
  padding: 10px;
}

input:focus {
  border: 2px solid #6a9bf0;
  border-right: none;
}

input:focus ~ button {
  border: 2px solid #6a9bf0;
  border-left: none;
}

button {
  cursor: pointer;
  border: 2px solid #4e60b6;
  border-left: none;
  border-radius: 0 10px 10px 0;
  display: block;
}

